<template>
	<div id="login">
		<c-title :hide="false" text='用户登录'></c-title>
		<div class="page">
		<mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="form.mobile" :attr="{ maxlength: 11 }" :state='form.mobileErr'></mt-field>
		<mt-field label="密码" placeholder="请输入密码" type="password" v-model="form.password" :state='form.passwordErr'></mt-field>

		</div>
		<div id="bts">
		<mt-button type="danger" @click="login" size="large">登录</mt-button>
		<mt-button type="default" @click="register" size="large">注册</mt-button>
		<p class="forget">忘记密码</p>
		</div>
	</div>
</template>

<script>
	//window.localStorage.isWeiXin
	import { mapState, mapMutations } from 'vuex';
	import { Field, Button } from 'mint-ui';
	import cTitle from 'components/title';
	export default {
		
		//name: 'login',
		data() {
			return {
				form: {
					mobile: '',
					mobileErr: '',
					password: '',
					passwordErr: '',

				},
				mydata:''
			}
		},

		components: {
			cTitle
		},
		mounted() {

		},

		methods: {
			 login() {
				if(!this.form.mobile || !this.form.password) {
					!this.form.mobile ? this.form.mobileErr = 'error' : this.form.mobileErr='';
					!this.form.password ? this.form.passwordErr = 'error' : this.form.passwordErr='';
					//this.$store.commit('alerts','请输入用户名和密码!');
					return
				} else {
					this.form.mobileErr = '';
					this.form.passwordErr = '';
				};
				this.form.type = window.localStorage.type;
				this.form.type = 5;
				$http.post('member.login.index', this.form).then((response)=>{
					console.log('信息',response);
					if(response.result == 1) {
						window.localStorage.myuserinfo = response.data;
						this.$store.commit('savemodel', response.data);
						this.$router.push({name:'home'});
					}
				});
				
			},register(){
				this.$router.push({name:'register'});
			}
		},
		activated(){

			console.log(this.$route.params.object_id);

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" rel="stylesheet/scss" scoped>

#login{margin-top: 50px;width: 100%;
	h1 {
		color: #42b983;
	}
	.mint-button--large {
		margin-top: 10px;
	}
	#bts{margin: auto 5px;}
.forget {
    color: #999;
    float: right;
}
}


	
</style>


